.category, .category:visited {
  text-decoration: none;
  position: relative;
  color: #ed1c24;
  cursor: pointer;
}

.category:after, .category:visited:after {
  content: '';
  height: 3px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  background: #ed1c24;
  transition: .5s;
}

.category:hover:after, .category:visited:hover:after {
  width: 100%;
  color: black;
}

