  cursor: pointer;
}

.source-btn.active {
  background: #ed1c24;
}

.pull-btn {
  /* background: rgba(255, 255, 255, 0.65); */
  height: 65px;
  width: 65px;
  border-radius: 50%;
  position: absolute;
  z-index: 99;
  right: 95%;
  top: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s;
}

/* .overLay .pull-btn:hover .blackshadow {
  transition: 1s;
  left: 0;
} */

.pull-btn.rotate {
  right: 59%;
}

.pull-btn.rotate svg {
  transform: rotate(180deg);
}

.pull-btn svg {
  height: 100%;
  width: 100%;
  color: white;
}

.hidden {
  opacity: 0;
  margin-bottom: 15px;
}

.shown {
  opacity: 1;
}

#nav_bar {
  transition: ease-in-out 0.25s;
}

.slick-slider {
  width: 100%;
}

.slides {
  position: relative;
}

.slick-prev, .slick-next {
  position: absolute;
  top: 50%;
  z-index: 99;
  width: 40px;
  height: 40px;
}

.slick-next:before, .slick-prev:before {
  font-size: 40px;
  line-height: 1;
  opacity: .75;
  color: black;
}

.slick-prev {
  left: -2%;
}

.slick-next {
  right: -2%;
}